import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

export default function PatientDetails({ patient }) {
  const [payments, setPayments] = useState(patient?.paymentHistory || []);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [newPayment, setNewPayment] = useState({
    date: new Date().toISOString().split("T")[0],
    description: "",
    amount: 0,
    status: "Pending",
  });

  if (!patient) return null;

  const addNewPayment = () => {
    setPayments([...payments, newPayment]);
    setIsModalOpen(false);
    setNewPayment({
      date: new Date().toISOString().split("T")[0],
      description: "",
      amount: 0,
      status: "Pending",
    });
  };

  const togglePaymentStatus = (index) => {
    const updatedPayments = [...payments];
    updatedPayments[index].status =
      updatedPayments[index].status === "Paid" ? "Pending" : "Paid";
    setPayments(updatedPayments);
  };

  return (
    <Card className="mt-4">
      <CardHeader>
        <CardTitle>Patient Details</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <h3 className="text-lg font-semibold mb-2">Personal Information</h3>
            <p>
              <strong>Full Name:</strong> {patient.name}
            </p>
            <p>
              <strong>Age:</strong> {patient.age}
            </p>
            <p>
              <strong>Date of Birth:</strong> {patient.dateOfBirth}
            </p>
            <p>
              <strong>Sex:</strong> {patient.sex}
            </p>
            <p>
              <strong>Phone:</strong> {patient.phoneNumber}
            </p>
            <p>
              <strong>Email:</strong> {patient.email}
            </p>
          </div>
          <div>
            <h3 className="text-lg font-semibold mb-2">Medical Information</h3>
            <p>
              <strong>Current Condition:</strong> {patient.condition}
            </p>
            <h4 className="font-semibold mt-2">Medical History</h4>
            <ul className="list-disc pl-5">
              {patient.medicalHistory.map((record, index) => (
                <li key={index}>
                  {record.date}: {record.diagnosis} - {record.treatment}
                </li>
              ))}
            </ul>
          </div>
        </div>
        <div className="mt-4">
          <h3 className="text-lg font-semibold mb-2">Payment History</h3>
          <div className="flex justify-between items-center mb-2">
            <Dialog open={isModalOpen} onOpenChange={setIsModalOpen}>
              <DialogTrigger asChild>
                <Button>New Payment</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Add New Payment</DialogTitle>
                </DialogHeader>
                <div className="grid gap-4 py-4">
                  <div className="grid grid-cols-4 items-center gap-4">
                    <Label htmlFor="description" className="text-right">
                      Description
                    </Label>
                    <Input
                      id="description"
                      value={newPayment.description}
                      onChange={(e) =>
                        setNewPayment({
                          ...newPayment,
                          description: e.target.value,
                        })
                      }
                      className="col-span-3"
                    />
                  </div>
                  <div className="grid grid-cols-4 items-center gap-4">
                    <Label htmlFor="amount" className="text-right">
                      Amount
                    </Label>
                    <Input
                      id="amount"
                      type="number"
                      value={newPayment.amount}
                      onChange={(e) =>
                        setNewPayment({
                          ...newPayment,
                          amount: parseFloat(e.target.value),
                        })
                      }
                      className="col-span-3"
                    />
                  </div>
                  <div className="grid grid-cols-4 items-center gap-4">
                    <Label htmlFor="status" className="text-right">
                      Status
                    </Label>
                    <Select
                      value={newPayment.status}
                      onValueChange={(value) =>
                        setNewPayment({ ...newPayment, status: value })
                      }
                    >
                      <SelectTrigger className="col-span-3">
                        <SelectValue placeholder="Select status" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="Paid">Paid</SelectItem>
                        <SelectItem value="Pending">Pending</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
                <Button onClick={addNewPayment}>Add Payment</Button>
              </DialogContent>
            </Dialog>
          </div>
          <table className="w-full">
            <thead>
              <tr>
                <th className="text-left">Date</th>
                <th className="text-left">Description</th>
                <th className="text-right">Amount</th>
                <th className="text-right">Status</th>
                <th className="text-right">Action</th>
              </tr>
            </thead>
            <tbody>
              {payments.map((payment, index) => (
                <tr key={index}>
                  <td>{payment.date}</td>
                  <td>{payment.description}</td>
                  <td className="text-right">${payment.amount}</td>
                  <td className="text-right">
                    <Badge
                      variant={
                        payment.status === "Paid" ? "success" : "warning"
                      }
                    >
                      {payment.status}
                    </Badge>
                  </td>
                  <td className="text-right">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => togglePaymentStatus(index)}
                    >
                      Toggle Status
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </CardContent>
    </Card>
  );
}
